body {
	margin: 0;
	padding: 0;
	font-family: Raleway;
	overflow-x: hidden;
	background-image: -webkit-gradient(linear, right top, left top, from(#f9ed32), color-stop(#ffbc24), color-stop(#ff893e), color-stop(#ff565d), to(#ee2a7b));
	background-image: -webkit-linear-gradient(right, #f9ed32, #ffbc24, #ff893e, #ff565d, #ee2a7b);
	background-image: -o-linear-gradient(right, #f9ed32, #ffbc24, #ff893e, #ff565d, #ee2a7b);
	background-image: linear-gradient(to left, #f9ed32, #ffbc24, #ff893e, #ff565d, #ee2a7b);
	background-repeat: no-repeat;
}



