.filter-container {
	background-color: #ffffff77;
	display: block;
	padding: 20px 5px;
}
.filter-header-container {
	display: -ms-flexbox;
	display: flex;
	cursor: pointer;
	-ms-flex-align: center;
	    align-items: center;
}
.expanded {
	-ms-flex-pack: center;
	    justify-content: center;
}
.collapsed {
	-ms-flex-pack: start;
	    justify-content: flex-start;
}
.filter-header {
	margin: 0px 30px;
}
.filters {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: start;
	    align-items: flex-start;
}
.langauge {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}
.country {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}
.options {
	text-align: left;
}
.checkbox {
	margin-right: 10px;
	width: 20px;
	height: 20px;
}



