.movie-item {
	background-color: #fff;
	padding: 5px 10px;
	margin: 5px;
	text-align: left;
	display: -ms-flexbox;
	display: flex;
	cursor: pointer;
}
.movie-item:hover {
	background-color: #ccccccdd;
}
.item-left {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	margin-right: 2vw;
}
.item-right {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}
.poster {
	width: 100%;
	-o-object-fit: contain;
	   object-fit: contain;
}
.title {
	font-size: 1.8em;
	color: #222;
}
.genre {
	font-size: 1em;
	font-weight: normal;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 2px;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.genre-item {
	background-color: transparent;
	border: 2px solid #422;
	color: #422;
	padding: 7px 7px;
	border-radius: 7px;
	margin: 0px 3px;
}



