.expanded-movie-item {
	background-color: #fff;
	-ms-flex: 1 1;
	    flex: 1 1;
	padding: 5px 10px;
	margin: 5px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
	text-align: center;
}
.close-btn {
	-ms-flex-item-align: end;
	    align-self: flex-end;
	font-size: 2em;
	cursor: pointer;
}
.close-btn:hover {
	background-color: #fb8;
	border: #fff;
}
.expanded-poster {
	width: 25%;
	-o-object-fit: contain;
	   object-fit: contain;
}
.expanded-rating {
	font-size: 1.5em;
}
.expanded-title {
	font-size: 1.9em;
	color: #222;
}
.expanded-summary {
	font-size: 1.2em;
}
.imdb-link {
	background-color: #fb0;
	text-decoration: none;
	padding: 2px 5px;
	border-radius: 7px;
	border: 2px solid #000;
	color: #000;
}
.expanded-genre {
	font-size: 1em;
	font-weight: normal;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 2px;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.expanded-genre-item {
	background-color: transparent;
	border: 2px solid #422;
	color: #422;
	padding: 7px 7px;
	border-radius: 7px;
	margin: 0px 3px;
}
.expanded-year {
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	line-height: 2;
}



