.App-header {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  width: 98%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 1%;
  height: 12vh;
  background-color: #ffffff77;
}
.App-logo-link {
  margin: 0% 5% 0% 2%;
  text-decoration: none;
}

.App-title {
  font-size: 2.7em;
  color: #422;
}
.search {
  width: 35vw;
  background-color: transparent;
  font-size: 1.1em;
  border: none;
  border-bottom: 2px solid #000;
  padding: 1vw;
  outline: none;
}
.search:focus {
  border: none;
  outline: none;
  border-bottom: 2px solid #fff;
}

@media screen and (max-width: 600px) {
  .App-logo {
    height: 25px;
  }
  .App-title {
    font-size: 1.5em;
  }
  .App-header {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
  .search {
    width: 60vw;
    font-size: 1em;
  }
  .search::-webkit-input-placeholder {
    font-size: 0.8em;
  }
  .search:-ms-input-placeholder {
    font-size: 0.8em;
  }
  .search::-ms-input-placeholder {
    font-size: 0.8em;
  }
  .search::placeholder {
    font-size: 0.8em;
  }
}



